/* Chat.css */
.chat-container {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.messages {
  max-height: 300px;
  overflow-y: auto;
}

.message {
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.message-form {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.message-form input {
  flex-grow: 1;
  margin-right: 10px;
  padding: 10px;
}

.message-form button {
  padding: 10px 20px;
}
