/* src/styles/MatchSuggestions.css */

.container {
  padding: 20px;
}

.match-card {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

.match-card .actions {
  margin-top: 10px;
}

.match-card .pending {
  color: orange;
  font-weight: bold;
  margin-top: 10px;
  display: block;
}
